import Layout from '@core/components/Layout'
import Curriculo from './Curriculo'

export default [
  {
    path: '/',
    component: Layout,
    children: [
      {
        path: '/',
        component: Curriculo,
        meta: {
          unrequiredAuth: true,
          title: 'Jeferson Capobianco'
        }
      }
    ]
  }
]



